
.wrapper{
  nav{
    display: flex;
    justify-content: flex-end;
    padding:0 10px;
    align-items: center;
    height:50px;
    ul{
      display: flex;
      list-style: none;
      li{
        display: flex;
        width:175px;
        align-items: center;
        justify-content: center;
        height:30px;
        background: black;
        color:white;
        margin-right: 10px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
}

section.main{
  height:calc(100vh - 50px);
  width:100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  .card{
    display: flex;
    flex-direction: column;
    transform: translateY(-20px);
    .card-btns{
      display: flex;
      flex-direction: column;
      margin-top: 30px;
      .btn-primary{
        border: 2px solid black;
        background: black;
        color:white;
      }
      .btn-secondary{
        background: white;
        border: 2px solid black;
        color:black;
      }
      div{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
      }
      button{
        min-width: 175px;
        padding:5px ;
        font-size: 1.2rem;
        background: white;
        font-family: 'Sora-Regular';
      }
      
    }
    h2{
      font-size: 3rem;
      font-weight: 100;
      margin-bottom: 20px;
    }
    .skills{
      .skill{
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        font-size: 1.5rem;
        &:last-child{
          margin-bottom: 0;
        }
        img{
          width:30px;
          margin-right: 10px;
        }
      }
    }
  }
}

