*,*::before,*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face{
  font-family: 'Sora-Medium';
  src: url('./fonts/Sora-Medium.ttf');
}
@font-face{
  font-family: 'Sora-Regular';
  src: url('./fonts/Sora-Regular.ttf');
}
body {
  margin: 0;
  font-family: 'Sora-Regular';
  font-weight: 100;
}

